import React from 'react';

function Info() {
  const articles = [
    {
      imageSrc: 'https://flowbite.com/docs/images/blog/image-1.jpg',
      title: 'Noteworthy technology acquisitions 2021',
      description: 'Here are the biggest enterprise technology acquisitions of 2021 so far, in reverse chronological order.',
      url: '#',
    },
    {
        imageSrc: 'https://flowbite.com/docs/images/blog/image-1.jpg',
        title: 'Noteworthy technology acquisitions 2021',
        description: 'Here are the biggest enterprise technology acquisitions of 2021 so far, in reverse chronological order.',
        url: '#',
      },
    {
        imageSrc: 'https://flowbite.com/docs/images/blog/image-1.jpg',
        title: 'Noteworthy technology acquisitions 2021',
        description: 'Here are the biggest enterprise technology acquisitions of 2021 so far, in reverse chronological order.',
        url: '#',
      },
      {
        imageSrc: 'https://flowbite.com/docs/images/blog/image-1.jpg',
        title: 'Noteworthy technology acquisitions 2021',
        description: 'Here are the biggest enterprise technology acquisitions of 2021 so far, in reverse chronological order.',
        url: '#',
      },
      
      
  ];

  return (
    <div className="flex flex-wrap justify-center">
      {articles.map((article) => (
        <div key={article.title} className="max-w-sm bg-white border border-gray-200 rounded-lg shadow dark:bg-gray-800 dark:border-gray-700 mx-3 my-5">
          <a href={article.url}>
            <img className="rounded-t-lg" src={article.imageSrc} alt="" />
          </a>
          <div className="p-5">
            <a href={article.url}>
              <h5 className="mb-2 text-2xl font-bold tracking-tight text-gray-900 dark:text-white">{article.title}</h5>
            </a>
            <p className="mb-3 font-normal text-gray-700 dark:text-gray-400">{article.description}</p>
            <a href={article.url} className="inline-flex items-center px-3 py-2 text-sm font-medium text-center text-white bg-blue-700 rounded-lg hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">
              Read more
              <svg aria-hidden="true" className="w-4 h-4 ml-2 -mr-1" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M10.293 3.293a1 1 0 011.414 0l6 6a1 1 0 010 1.414l-6 6a1 1 0 01-1.414-1.414L14.586 11H3a1 1 0 110-2h11.586l-4.293-4.293a1 1 0 010-1.414z" clipRule="evenodd"></path></svg>
            </a>
          </div>
        </div>
      ))}
    </div>
  );
}

export default Info;
